<template>
    <div class="page-login">
        <form class="login" method="post" @submit.prevent="loginForm">
            <h1>Login</h1>
            <transition name="fade">
                <div class="error" v-show="showError">{{errorMsg}}</div>
            </transition>
            <router-link v-show="status === 401" to="/register" class="register-link">Click here to become a member</router-link>
            <label class="icon-envelop">
                <input type="email" class="email" v-model="email" placeholder="Email..." required>
            </label>
            <label class="icon-lock">
                <input type="password" class="password" v-model="password" pattern="^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9]).{6,}$" oninvalid="this.setCustomValidity('Must contain minimum 6 characters and at least one uppercase, one lowercase and a number')" oninput="this.setCustomValidity('')" placeholder="Password..." required>
            </label>
            <button @submit="loginForm" type="submit">Lets Go</button>
        </form>
    </div>
</template>

<script>
export default {
    data() {
        return {
            email: '',
            password: '',
            errorMsg: '',
            showError: false,
            status: '',
            url: 'https://test-api.updivision.work/api/login?',
        }
    },
    methods: {
        loginForm:function() {
            const params = `email=${this.email}&password=${this.password}`;

            fetch(this.url + params, {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                },
            }).then(response => { 
                this.status = response.status;
                
                response.json()
                .then( (data) => { 
                    if(this.status === 200) {
                        this.$store.commit('userKey', data.accessToken);
                        this.$router.push({name: 'Products'});
                    }

                    if(this.status === 401) {
                        this.errorMsg = data.message;
                        this.showError = true;
                    }

                    if(this.status === 422) {
                        this.errorMsg = data.errors[Object.keys(data.errors)[0]][0];
                        this.showError = true;
                    }
                })
            }).catch(error => {
                this.errorMsg = error.message;
                this.showError = true;
            });
        }
    }
}
</script>

<style scoped>
.page-login {
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.5);
}
.login {
    padding: 0 20px 40px;
    display: flex;
    flex-direction: column;
}
.login h1 {
    text-align: center;
    color: #fff;
    font-size: 20px;
    font-weight: 700;
    padding: 25px;
    background: linear-gradient(to left, #7D20A3 0%, #AA46BC 100%);
    transform: translateY(-30px);
    letter-spacing: 1px;
    border-radius: 5px;
}
.login input {
    width: 100%;
}
.login .email {
    margin-top: 10px;
}
.login button {
    background-color: transparent;
    color: #E91E63;
    text-transform: uppercase;
    width: max-content;
    margin: 25px auto 0;
    padding: 5px;
}
.register-link {
    font-size: 20px;
    text-align: center;
    margin: 10px 0 30px;
}
.login label {
    margin: 0 15px;
}
</style>
